.Car {
  margin: 0 auto 15px;
  padding-bottom: 20px;
  border-radius: 5px;
  transition: border, box-shadow 0.3s;
}

.input {

}

$success-color: lightseagreen;
$error-color: lightcoral;

.input:active,
.input:focus {
  outline: none;
}

.input.green {
  border: 1px solid $success-color;
}

.input.red {
  border: 1px solid $error-color;
}

.input.bold {
  font-weight: 700;
  border: 2px solid $success-color;
}